<template>
<nav>
    <div class="nav king-horizontal-nav11 " id="horizontal_nav_demo11">
        <div class="navbar-header">
            <a @click="backHome" class="navbar-brand">
                <img src="./logo10.png" class="logo">
                <span class="text-white logo-tip">日志检索</span>
            </a>
        </div>
        <div class="fr">
            <ul class="nav navbar-nav">
                <li class="app-select mr20" @click.stop="stopDocClick">
                    <div class="sumoselect_wrapper" id="sumoselect_demo1">
                        <a class="king-btn king-btn-primary king-btn-select text-over-hide" :class="{'active':isOpen}"  @click="toggleSelect">{{appName}}<i class="fa fa-caret-down" :class="{'active':isOpen}"></i></a>
                        <ul v-if="isOpen">
                            <li v-for="(list,index) in appList" @click="changeApp(index)" v-if="myId != parseInt(list.app_id)"><a class="text-over-hide">{{list.app_name}}</a></li>
                        </ul>
                        <select name="somename" class="SlectBox hide" v-model="myId">
                            <option v-for="list in appList" :value="list.app_id">{{list.app_name}}</option>
                        </select>
                    </div>
                </li>
                <li><router-link to="/home">首页</router-link></li>
                <li><router-link to="/host">采集项管理</router-link></li>
                <li><router-link to="/history">历史记录</router-link></li>
                <li><router-link to="/help">帮助</router-link></li>
            </ul>
            <div class="navbar-header ml20">
                <ul class="nav">
                    <li class="user-info">
                        <a href="javascript:;">
                            <span class="f12">{{username}}</span><img src="./avatar.png" class="img-rounded">
                        </a>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</nav>
</template>

<script>
import {getCookie} from 'common/js/functions.js';
import {saveToLocal, loadFromLocal} from 'common/js/store';

export default {
  name: 'header',
  props: {
    appList: {
        type: Array
    }
  },
  data () {
    return {
        myId: this.$parent.appId,
        username: clientName,
        isOpen:false
    };
  },
  computed: {
    appName () {
        let name = "";
        for (var i = 0; i < this.appList.length; i++) {
            if(parseInt(this.appList[i]['app_id']) === this.myId){
                name = this.appList[i]['app_name'];
            }
        };
        return name;
    }
  },
  watch: {
    'myId' () {
        this.$parent.appId = this.myId;
        if(this.$parent.$refs.innerview.view === 'history'){
           this.$parent.$refs.innerview.renderData();
        }
        if(this.$parent.$refs.innerview.view === 'host'){
           this.$parent.$refs.innerview.host = [];
           this.$parent.$refs.innerview.renderData();
        }
        if(this.$parent.$refs.innerview.view === 'result'){
           this.$parent.$refs.innerview.renderData();
           this.$parent.$refs.innerview.start = 0;
        }
    }
  },
  methods: {
    toggleSelect () {
        this.isOpen = !this.isOpen;
    },
    stopDocClick () {},
    changeApp (index) {
        this.myId = parseInt(this.appList[index]['app_id'])
        saveToLocal(clientName, 'app_id', parseInt(this.myId));
        let thisView = this.$parent.$refs.innerview.view;
        this.isOpen = false;
        if (thisView === 'home' || thisView === 'result') {
            let checkData = new FormData();
            checkData.append('app_id',this.myId);
            checkData.append('csrfmiddlewaretoken',getCookie('csrftoken'));
            this.$http.post(site + 'check_app_config/',checkData, {
              emulateJSON: true
            }).then(response => {
                response = response.body;
                saveToLocal(clientName, 'logSearchNoticed', false);
                if (thisView === 'home') {
                    this.$parent.$refs.innerview.renderData();
                }
            })            
        };
    },
    backHome () {
        this.$router.push('/');
    }
  },
  created () {

  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" rel="stylesheet/scss" scoped>
.king-horizontal-nav11{
    min-width: 1200px;
    position: relative;
    z-index: 15;
    box-shadow: 0 0px 1px #ccc;
    .app-select{
        margin-top: 17px;
    }
    .navbar-brand{
        font-size: 0;
        cursor: pointer;
        padding: 15px 0 0 15px;
    }
    li a{
        color: #deefff;
    }
    .sumoselect_wrapper{
        position: relative;
        .king-btn-select{
            width: 160px;
            height: 36px;
            line-height: 34px;
            background: #313B4C;
            border:1px solid #464f5e;
            border-radius: 2px;
            text-align: left;
            .fa{
                transition:all 0.3s;
                position: absolute;
                right: 8px;
                top: 10px;
                &.active{
                    transform: rotate(180deg);
                }
            }
            &:hover{
                opacity: 1;
                color: #deefff;
            }
            &.active{
                border:1px solid #7A9CD3;
                background: white;
                color: #79889d;
                &:hover{
                    color: #79889d;
                }
            }
        }
        ul{
            position: absolute;
            left: 0;
            top: 35px;
            min-width: 160px;
            padding-left: 0;
            background: white;
            border: 1px solid #7A9CD3;
            border-top: none;
        }
        li{
           a{
                display: block;
                padding-left: 15px;
                padding-right: 15px;
                width: 100%;
                height: 30px;
               line-height: 30px;
               color: #79889d;
               max-width: 154px;
               &:hover{
                color: white;
               }
           }
           &:hover{
              background:#1E90FF;
              color: white;
              cursor: pointer;
           }
        }
    }
    .logo{
        display: inline-block;
        vertical-align: middle;
    }
    .logo-tip {
        top: 20px;
        left: 120px;
        font-size: 18px;
        vertical-align: middle;
        padding-left: 14px;
    }
}
@media (max-width: 768px){
    .navbar-nav>li {
        float: left;
    }
}
</style>



