<template>
<div class="form-group mr5 checkbox form-hd">
    <label class="ml30">
    <span class="king-asterisk left-title">模块</span>
    <div class="show-cols-wrapper ml50"  @click.stop="stopDocClick">
        <a class="king-btn king-default text-over-hide pr30" @click.stop.prevent="showOptionsWrapper">{{moduleSelected}}<span v-show="!moduleSelected">请选择</span><span class="caret"></span></a>
        <div class="show-cols" v-show="showOptionsList" @click.stop="showColsBox">
          <div>
            <label class="label-box" v-for="m in moduleList">
                <div class="fl" @click="fnSelected">
                    <input type="checkbox" v-model="m.value">
                    <span class="form-checkbox-icon"></span>
                </div>
                <div class="fl form-control-lh">{{m.text}}</div>
            </label>
          </div>                  
        </div>                
    </div>
    </label>
</div>
</template>

<script type="text/javascript">
import {getCookie} from 'common/js/functions';
export default{
    name: 'select',
    props:['selectData','selected','appId'],
    data () {
      return {
        data :[],
        showString: null,
        showOptions: [],
        showOptionsList: false,
        moduleSelected:'',
        moduleList: []
      }
    },
    methods: {
      stopDocClick () {},
      fnSelected () {
        let result = [];
        for (let i = 0; i < this.moduleList.length; i++) {
          if (this.moduleList[i]['value']) {
            result.push(this.moduleList[i]['text']);
          }
        };
        result = result.join(',');
        this.moduleSelected = result;
      },
      showOptionsWrapper(){
          this.showOptionsList = !this.showOptionsList;
          this.$parent.modulesEmpty = false;
      },
      showColsBox(){

      }
    },
    created () {
      let self = this;
      let moduleData = new FormData();
      moduleData.append('app_id', this.appId);
      moduleData.append('csrfmiddlewaretoken',getCookie('csrftoken'));

      this.$http.post(site + 'get_modules/', moduleData).then(response => {
        response = response.body;
        for (let i = 0; i < response.data.length; i++) {
          let v = false;
          for (var j = 0; j < this.selected.length; j++) {
             if(this.selected[j]==response.data[i]){
                v = true;
             }
          };
          self.moduleList[i] = {
            'text': response.data[i],
            'value': v
          };
          this.moduleSelected = this.selected.join(',');
        };
      });
    },
    mounted () {
      document.onclick = (event) => {
        this.showOptionsList = false;
      };
    }
};
</script>

<style scoped lang="scss">
.left-title{
  display: inline-block;
  padding-top: 7px;
  height: 36px;
}
.show-cols-wrapper{
    z-index: 100;
    position: relative; 
    display: inline-block;
    width:383px;
    &>a{
      height: 36px;
      width: 100%;
      background: white;
      cursor: default;
      text-align: left;
      .caret{
        position: absolute;
        right:10px;
        top: 16px;
      }
    }
    .show-cols{
      position: absolute;
      top: 34px;
      left: 0;
      width: 100%;
      background: white;
      border: 1px solid #ccc;
      &>div{
        label{
          display: block;
          width: 100%;
          height: 100%;
          overflow: hidden;
          padding:10px 8px 5px;
          vertical-align: top;
        } 
      }
      &>div+div{
        border-top: 1px solid #ccc;
      }
    }
  }  
  .modules-empty{
    .show-cols-wrapper{
      .king-btn{
        border: 1px solid red!important;
      }
    }
  }
</style>



