<template>
  <div class="king-step-box clearfix">
    <ul>
      <li class="king-step-start" v-for="(v,k) in currentStatus">
        <template v-if="k=='finish_status'">
          <div class="king-syep-horizontal clearfix" v-show="v == 0">
            <span class="king-step-num "></span>
            <span class="king-step-title bg-success text-white pl30">删除成功</span>
          </div>
          <div class="king-syep-horizontal clearfix step-failed" v-show="v == 1">
            <span class="king-step-num"></span>
            <span class="king-step-title text-white pl30">删除异常</span>
          </div>
        </template>
        <template v-else>
          <div class="king-syep-horizontal clearfix">
          <span class="king-step-num ">{{k}}</span>
          <span class="king-step-title">{{v.title}}</span>
          </div>
          <div class="king-step-vertical" v-if="v.status != 'finished'">
            <p v-if="v.status == 'success'">执行成功 <span class=" ml10  king-badge bg-success"><i class="fa fa-check"></i></span></p>
            <p v-if="v.status == 'working'">执行中 <img src="./loading_2_24x24.gif" class="ml10"></p>
            <p v-if="v.status == 'failed'">执行异常 <span class=" ml10  king-badge bg-danger"><i class="fa fa-close"></i></span></p>
          </div>
        </template>
      </li>
    </ul>
  </div>
</template>

<script>
import {getCookie} from 'common/js/functions';
export default {
  props: {
    detailid: {
      type: Number
    },
    appId: {
      type: Number
    }
  },
  data () {
    return {
      currentStatus: {},
      statusType: ['执行成功','执行中','执行失败']
    };
  },
  methods: {
    _requestDetail (detailData) {
      let self = this;
      this.$http.post(site + 'get_host_status/', detailData,{
              emulateJSON: true
        }).then(response => {
        response = response.body;
        if (response.result) {
          self.currentStatus = response.status;
        };
      });
    }
  },
  created () {
    let self = this;
    let detailData = {
      'app_id': this.appId,
      'id': this.detailid,
      'csrfmiddlewaretoken': getCookie('csrftoken')
    }

    let t = 0;

    this._requestDetail(detailData);
    t = setInterval(() => {
      this.$http.post(site + 'get_host_status/', detailData,{
        emulateJSON: true
      }).then(response => {
        response = response.body;
        if (response.result) {
          if(response.status.finish_status === 0 || response.status.finish_status === 1 || !this.$parent.detailShow){
            clearInterval(t);
          }
          self.currentStatus = response.status;
        };
      });
    },800);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" rel="stylesheet/scss" scoped>
.king-step-box{
  font-size:0;
    padding:50px 228px 50px 80px;
    .king-step-start{
      .king-step-title{
        text-align: left;
        padding-left: 15px;
        border:1px solid #5bd18b;
      }
      .king-step-num{

        background: #5bd18b;
      }
      .king-step-vertical::before{
        content:"";
        position:absolute;
        left:-19px;
        width:1px;
        background:#5bd18b;
        height:100%;
        top:0;
      }
    }
    .king-step-current{
      .king-step-title{
        border:1px solid #5bd18b;
      }
      .king-step-num{
        background: #5bd18b;
      }
      .king-step-vertical::before{
        content:"";
        position:absolute;
        left:-19px;
        width:1px;
        height:100%;
        top:0;
        border-left: 1px dashed #ccc;
      }
    }
    .king-step-waiting{
      .king-badge{
        border:1px solid #ccc;
        color:#ccc;
        background:#fff;
        padding:2px;
      }
      .king-step-title{
        border:1px solid #ccc;
        color:#ccc;
      }
      .king-step-num{
        background: #ccc;
      }
      p{
        color:#ccc;
      }
    }
    .king-step-num{
      font-size:20px;
      font-weight:block;
      color:#fff;
      text-align: center;
      line-height:42px;
      border-radius:50% 0 0 50%;
      display:inline-block;
      width:44px;
      height:42px;
      float:left;
    }
    .king-step-title{
      text-align: center;
      display:inline-block;
      font-size:16px;
      color:#666;
      width:182px;
      height:42px;
      line-height:42px;
      border-radius: 0 21px 21px 0;

      box-sizing: border-box;
      float:left;
    }
    .king-step-vertical{
      position: relative;
      margin-left:63px;
      padding:20px 0 24px 0;
    }
    .step-failed span{
      background: #ff7979!important;
      border-color: #ff7979!important;
    }
    p{
      font-size: 14px;
      color:#666;
      line-height:22px;
    }
  }
</style>



