<template>
<div class="searchbox">
    <div class="search-box">
        <div class="search-button-arrow">
            <input class="search-input" type="text" v-model="searchKey" @keyup.enter="getSearchLog" placeholder="搜索">
            <button @click="getSearchLog">搜索一下</button>
        </div>
        <p>找到<span class="pl5 pr5">{{myResult.total?myResult.total:0}}</span>条结果（用时<span class="pl5 pr5">{{myResult.took?myResult.took:0}}</span>秒）</p>
    </div>
</div>
</template>

<script>
import {urlParse} from 'common/js/util';
import {getCookie,trimRound} from 'common/js/functions';
import {saveToLocal, loadFromLocal} from 'common/js/store';

export default {
  props: {
    myResult: {
        type: Object
    },
    appId: {
        type: Number
    },
    openAll: {
        type: Boolean
    },
    timeRound: {
      type: String
    }
  },
  data () {
    let logSearchKey =  loadFromLocal(clientName, 'logSearchKey', '');
    return {
      searchKey: logSearchKey
    };
  },
  watch: {
    'searchKey' () {
      saveToLocal(clientName, 'logSearchKey', this.searchKey);
    }
  },
  methods: {
    getSearchLog () {
      /* 组件里面如果直接对父组件传进来的myResult进行修改会报错 */
        var newLogData = {
          'size':30,
          'start':0,
          'key': trimRound(this.searchKey),
          'app_id': this.appId,
          'csrfmiddlewaretoken':getCookie('csrftoken')       
        };

        this.$http.post(site + 'search_log/', newLogData,{
            emulateJSON: true
        }).then(response => {
          response = response.body;
            if (response.result) {
              response.data.log_list.forEach((item) => {
                item.shortview = !this.openAll;
              });
              this.$parent.result = response.data;
              if (response.code === '20012') {
                this.$parent.noResult = response.message;
              }else{
                this.$parent.noResult = '暂无数据！';
              }
            }else{
              this.$parent.result = {};
            }
        });
        // 渲染图表
        this.$parent.changeChart(this.timeRound);
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" rel="stylesheet/scss" scoped>
.search-box {
    padding-top: 48px;
}
.search-box input{
    font-size:18px;
    padding:0 15px;
    width: 540px;
    height:42px;
    border-radius:2px 0px 0px 2px;
    border:none;
    line-height:42px;
    vertical-align: top;
    border: 1px solid #eee;
    border-right: none;
    &:focus{
      outline: none;
      box-shadow: 0px 0 3px #eee;
    }
}
.search-box button{
    background:#4b9fff;
    width:120px;
    height:42px;
    border:none;
    color:#fff;
    font-size:18px;
    border-radius:0 2px 2px 0;
    line-height:42px;
}
.search-box .search-button-arrow{
    width: 690px;
    font-size:0;
    margin:auto auto;
}
.search-box p{
    width: 690px;
    margin: auto;
    padding-top: 12px;
    text-align:left;
    font-size:14px;
    color:#999999;
}
.search-box p span{
    padding-left:22px;
}
</style>



