<template>
<div class="help">
  <div class="panel panel-default">
    <div class="panel-body p30">
      <ul class="king-timeline">
          <li class="time-label">
            <span class="king-bg-blue pr10 pl10">日志检索三部曲</span>
          </li>
          <li>
              <i class="fa king-bg-blue">1</i>
              <div class="king-timeline-item">
                  <h3 class="king-timeline-header mb15">
                      <b>部署采集项</b>
                  </h3>
                  <div class="pl10">
                    <p>1、入口：<b class="text-primary">采集项管理</b> <i class="fa fa-long-arrow-right"></i> <b class="text-primary">新建采集项</b></p>
                    <p>2、采集项的IP地址可以以模块来添加</p>
                    <p>3、日志路径可以用简单的通配符，如/data/*.log，类似linux中的ls *.log匹配</p>
                    <p>4、当通配符中有一些日志不需要采集的，可以通过排除文件来实现</p>                    
                  </div>

                  <div class="king-timeline-body">
                      <img :src="imgSrc('images/help1.png')" alt="" style="width:666px;">
                  </div>
              </div>
          </li>
          <li>
              <i class="fa king-bg-blue">2</i>
              <div class="king-timeline-item">
                  <h3 class="king-timeline-header mb15">
                    <b>日志检索搜索页</b>
                  </h3>
                  <div class="pl10">
                    <p>1、搜索历史显示一天内最近搜索的、次数最多的关键字</p>
                    <p>2、搜索的关键字可以用*号通配符</p>                   
                  </div>
                  <div class="king-timeline-body">
                      <img :src="imgSrc('images/help2.png')" alt="" style="width:666px;">
                  </div>
              </div>
          </li>
          <li>
              <i class="fa king-bg-blue">3</i>
              <div class="king-timeline-item">
                  <h3 class="king-timeline-header mb15">
                    <b>结果展示</b>
                  </h3>
                  <div class="pl10">
                    <p>1、日志是按行匹配的，匹配到的关键字会高亮</p>
                    <p>2、可以自定义显示的列，日志内容是必显示项</p>                   
                  </div>
                  <div class="king-timeline-body">
                      <img :src="imgSrc('images/help3.png')" alt="" style="width:1000px;">
                  </div>
              </div>
          </li>
          <li>
              <i class="fa fa-clock-o king-bg-gray"></i>
          </li>
      </ul>
    </div>
  </div>
</div>
</template>

<script>

export default {
  name: 'help',
  props: {},
  data () {
    return {
      staticSite: staticSite
    }
  },
  methods: {
    imgSrc (src) {
      return this.staticSite + src;
    }
  },
  created () {
    let appDom = document.getElementById('app');
    appDom.onscroll = null;
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
.help{
  padding: 50px 20px 100px;
  .time-label{
    margin-bottom: 40px;
  }
  .king-timeline-header{
    padding-top: 6px!important;
    font-size: 16px!important;
  }
}
</style>


