<template>
  <div class="host">
    <div class="panel panel-default table7_demo">
        <div class="panel-body pl40 pr40">
            <span class="title f20 pull-left">采集项管理</span>
            <form class="form-inline pull-right">
                <div class="form-group mr15" @click="newHost">
                    <a class="king-btn-demo king-btn king-primary" title="新建采集项">
                    新建采集项
                    </a>
                </div>
                <div class="checkbox form-hd toggle-all mr15">
                    <label @click="showOpenType">
                        <div class="fl">
                            <input type="checkbox" v-model="openAll">
                            <span class="form-checkbox-icon"></span>
                        </div>
                        <div class="fl form-control-lh">全部展开</div>
                    </label>
                </div>
                <div class="input-group">
                  <input type="text" class="form-control mr0 host-search" placeholder="请输入模块/IP/路径 , 按回车键搜索" v-model="filterTxt" @keypress.enter.stop.prevent="searching">
                </div>
            </form>
        </div>
        <div>
            <table class="table mb0" id="host_table">
                <thead>
                    <tr class="active">
                        <th style="width:30px;"></th>
                        <th class="w200">模块/IP信息</th>                        
                        <th>采集路径</th>
                        <th class="w300">采集状态</th>
                        <th class="text-center w200">操作</th>
                    </tr>
                </thead>
                <tbody id="datas">
                    <template v-for="(item,index) in host">
                      <tr class="">
                        <td class="p0">
                          <span class="pl10 pointer" @click="showIpTr(index)">
                            <i class="log-icon-open f18 vm icon-color" v-show="item.shortview"></i>
                            <i class="log-icon-fold f18 vm icon-color" v-show="!item.shortview"></i>
                          </span>
                        </td>
                        <td @click="showIpTr(index)" class="pointer">{{item.module}}</td>
                        <td style="padding:8px 11px;">
                            <div :title="p" class="fl vm mr10 text-over-hide" style="line-height:30px;max-width:150px;" v-for="p in item.paths" v-if="p"><i class="fa fa-save mr5 f16"></i>
                            <span v-html="p" class="paths-text"></span></div>
                        </td>
                        <td @click="showIpTr(index)" class="pointer"><strong class="text-success">{{item.host_count.normal_count}}台正常</strong> <strong class="text-explode">|</strong> <strong class="text-danger">{{item.host_count.unnormal_count}}台异常</strong></td>
                        <td class="text-center">
                            <div class="king-btn-split" :class="{'is-disabled':!item.ip_total}" @click="resetHost(item.ip_total, item, 'module')">
                                <a class="king-btn king-warning king-btn-mini">
                                    <i class="fa fa-pencil-square-o"></i>
                                </a>
                                <a class="king-btn king-warning king-btn-mini">重新配置</a>
                            </div>
                            <div class="king-btn-split" @click="undercarriage(item.module)">
                                <a class="king-btn king-danger king-btn-mini">
                                    <i class="fa fa-close fa-fw"></i>
                                </a>
                                <a class="king-btn king-danger king-btn-mini">删除</a>
                            </div>
                        </td>
                      </tr>
                      <transition name="fade">
                      <tr class="ip-tr" :class="{'in': !item.shortview}" v-show="!item.shortview">
                        <td colspan="5" class="p0">
                          <table class="table mb0 table-inner">
                              <tbody>
                                <tr v-for="(ipItem,index) in item.hosts">
                                  <td style="width:30px;" class="p0"></td>
                                  <td class="w200 text-left">{{ipItem.ip}}</td>
                                  <td></td>
                                  <td class="w300 text-left">
                                  <span :class="{'text-primary' : ipItem.publish_status == '等待中', 'text-warning':ipItem.publish_status == '发布中' || ipItem.publish_status == '删除中','text-success':ipItem.publish_status == '采集中','text-danger':ipItem.publish_status == '发布异常' || ipItem.publish_status == '删除异常' || ipItem.publish_status == '状态异常'}">{{ipItem.publish_status}}</span>
                                  <img src="./loading_2_16x16.gif" class="vt ml5" v-if="ipItem.publish_status == '发布中' || ipItem.publish_status == '删除中'">
                                  </td>
                                  <td class="text-center w200">
                                    <template v-if="ipItem.publish_status != '采集中'">
                                      <div class="king-btn-split" @click="openResetIp(ipItem.ip,item.paths,item.ex_files,item.module)" v-if="item.ip_total">
                                          <a class="king-btn king-warning king-btn-mini">
                                              <i class="fa fa-pencil-square-o"></i>
                                          </a>
                                          <a class="king-btn king-warning king-btn-mini">重新下发</a>
                                      </div>
                                    </template>
                                    <div class="king-btn-split v-hide">
                                        <a class="king-btn king-danger king-btn-mini">
                                            <i class="fa fa-close fa-fw"></i>
                                        </a>
                                        <a class="king-btn king-danger king-btn-mini">删除</a>
                                    </div>
                                  </td>                                
                                </tr>                                
                              </tbody>
                          </table>                          
                        </td>
                      </tr>
                      </transition>
                    </template>
                </tbody>
                <tfoot>
                  <tr>
                    <td colspan="6" class="table-page pr15">
                      <div v-if="loading" class="no-data"><img src="./loading_2_24x24.gif"></div>
                      <div v-if="!pageLen && !loading" class="no-data">暂无数据！</div>
                      <ul class="pagination pagination1 pagination-sm" v-if="pageLen">
                          <li :class="{'disabled': !pageLen || this.currentPage === 0}">
                              <a href="javascript:;" @click="firstPage">
                                  <i class="king-icon-page-first"></i>
                              </a>
                          </li>
                          <li :class="{'disabled': !pageLen || this.currentPage === 0}">
                              <a href="javascript:;" @click="prevPage">
                                  <i class="king-icon-page-prev"></i>
                              </a>
                          </li>
                          <li v-for="index in pageLen" :class="{'active':index == currentPage+1}" v-show="index-1 == currentPage || index == currentPage || index-2 == currentPage"><a href="javascript:;" @click="turnPageTo(index)">{{index}}</a></li>
                          <li :class="{'disabled': !pageLen || currentPage === pageLen-1}">
                              <a href="javascript:;" @click="nextPage">
                                  <i class="king-icon-page-next"></i>
                              </a>
                          </li>
                          <li :class="{'disabled': !pageLen || currentPage === pageLen-1}">
                              <a href="javascript:;" @click="lastPage">
                                  <i class="king-icon-page-last"></i>
                              </a>
                          </li>
                      </ul>
                    </td>
                  </tr>
                </tfoot>
            </table>
        </div>
    </div>
    <transition name="move">
      <div class="log-pop" v-if="winShow" @click="blurClose">
          <span class="pop-close" @click="winClose">关闭</span>
          <div class="new-log-host clearfix" @click.stop="stopDocClick">
              <div class="new-host-title"><span v-show="!detailShow">{{winType == 'module'?'配置采集项':'新建采集项'}}</span><span v-show="detailShow">详情</span></div>
              <v-win :win-data="winData" :win-type="winType" :app-id="app_id" v-if="!detailShow" :app-list="appList"></v-win>
              <div v-if="detailShow" class="host-step">
                <v-offdetail :detailid="detailid" :app-id="app_id" :showed="detailShow"></v-offdetail>
              </div>
          </div>
      </div>
    </transition>
    <transition name="fade">
        <div class="undercarriage-wrapper" v-if="underWindowShow">
          <div class="undercarriage-mark"></div>
          <div class="undercarriage-window">
            <div class="title">
              <span class="f18">提示</span> <i class="close-icon" @click="undercarriageHide"></i>
            </div>
            <div class="content">
              <div class="king-notice-box king-notice-warning">
                  <p class="king-notice-text f20">
                      <span v-show="!offWrong">您是否确认删除该采集项</span>{{offWrong}}
                  </p>
              </div>
            </div>
            <div class="footer" v-if="!offWrong">
                <a class="king-btn king-radius king-primary" :class="{'king-disabled':undercarriageing}" title="确认" @click="undercarriageDone" ref="undercarriageBtn">确认</a>
                <a class="king-btn-demo king-btn king-radius king-default" title="取消" @click="undercarriageHide">取消</a>
            </div>
            <div class="footer" v-if="offWrong">
                <a class="king-btn-demo king-btn king-radius king-default" title="取消" @click="undercarriageHide">关闭</a>
            </div>
          </div>
        </div>
    </transition>
    <transition name="fade">
        <div class="undercarriage-wrapper" v-if="resetIp">
          <div class="undercarriage-mark"></div>
          <div class="undercarriage-window">
            <div class="title">
              <span class="f18">提示</span> <i class="close-icon" @click="resetIpHide"></i>
            </div>
            <div class="content">
              <div class="king-notice-box king-notice-warning">
                  <p class="king-notice-text f20">
                      <span v-show="!resetIpErr">您是否确认重新下发配置</span>{{resetIpErr}}
                  </p>
              </div>
            </div>
            <div class="footer" v-if="!resetIpErr">
                <a class="king-btn king-radius king-primary" :class="{'king-disabled':undercarriageing}" title="确认" @click="resetIpDone">确认</a>
                <a class="king-btn-demo king-btn king-radius king-default" title="取消" @click="resetIpHide">取消</a>
            </div>
            <div class="footer" v-if="resetIpErr">
                <a class="king-btn-demo king-btn king-radius king-default" title="取消" @click="resetIpHide">关闭</a>
            </div>
          </div>
        </div>
    </transition>
  </div>
</template>

<script>
import searchbox from 'components/searchbox/searchbox';
import selects from 'components/select/select';
import win from 'components/win/win';
import offdetail from 'components/offdetail/offdetail';
import {getCookie, checkBlank, unescape} from 'common/js/functions.js';
import {urlParse} from 'common/js/util';
import {saveToLocal, loadFromLocal} from 'common/js/store';
import Vue from 'vue';

export default {
  name: 'host',
  props: {
    appList: {
      type: Array
    },
    prop: {
        type: Object
    }
  },
  data () {
    return {
        host: [],
        winShow: false,
        detailShow: false,
        winData: {},
        underWindowShow: false,
        undercarriageModule: '',
        undercarriageing: false,
        currentPage: 0,
        pageNum: 10,
        openAll: false,
        app_id: null,
        searchTxt: '',
        filterTxt:'',
        detailid: null,
        offWrong: '',
        resetIpErr: '',
        filterVal: '',
        total: 0,
        statusTimer: 0,
        view: 'host',
        loading: false,
        paths:'',
        ips:'',
        ex_files:'',
        appId: null,
        winType: '',
        oStart: 0,
        resetIp: false,
        resetIpDetail: {}
    };
  },
  computed: {
    pageLen () {
      return Math.ceil(this.total / this.pageNum);
    },
    hostFilter () {
      var self = this
      return self.host.filter(function (item) {
        return item.details.ip.indexOf(self.searchTxt) !== -1 || item.details.paths.indexOf(self.searchTxt) !== -1;
      })
    },
    start () {
      return this.currentPage * this.pageNum;
    },
    currentApp () {
          let appName = '';
          for (var i = 0; i < this.appList.length; i++) {
            if(this.appList[i]['app_id']==this.appId){
              appName = this.appList[i]['app_name'];
            }
          };
          return appName;
    }
  },
  methods: {
    showIpTr (index) {
      this.host[index].shortview = !this.host[index].shortview;
      if (this.host[index].shortview) {this.openAll = false};
      var len = 0;
      for (var i = 0; i < this.host.length; i++) {
          if (!this.host[i].shortview) {
            len++;
          };
      };
      if (len == this.host.length) {
        this.openAll = true;
      };
    },
    checkLong (dom,item) {
      let div = dom.querySelectorAll('.div-show')[0];
      let divHide = dom.querySelectorAll('.div-hidden')[0];
      let divWidth = div.clientWidth;
      let divHideWidth = divHide.clientWidth;

      if (divHideWidth > divWidth || item.details.module.split(',').length>2) {
        Vue.set(item,'tooLong',true);
      }else{
        Vue.set(item,'tooLong',false);
      }

    },
    newHost () {
        var obj = {
          'details': {
              'ex_files': '',
              'ip': '',
              'module': '',
              'paths': '',
              'publish_status': ''
          },
          'ip': '',
          'module': '',
          'paths': [],
          'publish_status': ''
        }
        this.winType = 'create';
        this.winData = obj;
        this.winShow = !this.winShow;     
    },
    needShow (index) {
      if (this.currentPage * this.pageNum <= index && index < (this.currentPage + 1) * this.pageNum) {
        return true;
      } else {
        return false;
      }
    },
    moduleShow (shortview, index, module) {
      if (shortview) {
        if (module.split(',').length > 2) {
          if (index < 1) return true;
        } else {
          if (index < 2) return true;
        }
      } else {
        return true;
      }
    },
    searching () {
      if (this.pageLen) {
        this.currentPage = 0;
      };
      this.host = [];
      this.refresh(this);
    },
    turnPageTo (index) {
      this.currentPage = index - 1;
      this.refresh(this);
    },
    prevPage () {
      if (!this.pageLen) {
        return;
      };
      if (this.currentPage === 0) {
        return;
      };
      this.currentPage = this.currentPage - 1;
      this.refresh(this);
    },
    nextPage () {
      if (!this.pageLen) {
        return;
      };
      if (this.currentPage === this.pageLen - 1) {
        return;
      };
      this.currentPage = this.currentPage + 1;
      this.refresh(this);
    },
    firstPage () {
      if (!this.pageLen) {
        return;
      };
      if (this.currentPage === 0) {
        return;
      };
      this.currentPage = 0;
      this.refresh(this);
    },
    lastPage () {
      if (!this.pageLen) {
        return;
      };
      if (this.currentPage === this.pageLen-1) {
        return;
      };
      this.currentPage = this.pageLen - 1;
      this.refresh(this);
    },
    showOpenType () {
      this.host.forEach((item) => {
        if (this.openAll) {
          item.shortview = false;
        } else {
          item.shortview = true;
        }
      });
    },
    toggleShow (detail) {
      let sText;
      if(document.selection){ // ie
        sText = document.selection.createRange().text;
      }else{
        sText = window.getSelection().toString();
      }
      if(sText)return;
      if (!detail.shortview) {
        this.openAll = false;
      };
      detail.shortview = !detail.shortview;
    },
    showDetail (id) {
      this.detailid = parseInt(id);
      this.detailShow = true;
      this.winShow = !this.winShow;
    },
    resetHost (ipTotal,item, str) {
      if (!ipTotal) {return};
      let ipStr = '';
      if (str == 'reset') {
        ipStr = ip;
      };
      let obj = {
        'details': {
            'ip': ipStr,
            'module': item.module,
            'publish_status': ''
        },
        'ip': ipStr,
        'module': item.module,
        'paths': item.paths,
        'publish_status': ''
      }
      // 过滤空路径
      var finalPath = [];
      item.paths.map(function(v, i) {
        if (v) {
          finalPath.push(v);
        };
      })
      obj.details.paths = finalPath.join(',');
      // 过滤空排除路径
      var finalEx = [];
      item.ex_files.map(function(v, i) {
        if (v) {
          finalEx.push(v);
        };
      })
      obj.details.paths = finalPath.join(',');
      obj.details.ex_files = finalEx.join(',');
      this.winData = obj;
      this.winType = str;
      this.winShow = !this.winShow;
    },
    blurClose () {
      this.winClose();
    },
    stopDocClick () {},
    winClose () {
      this.winShow = false;
      this.detailShow = false;
    },
    undercarriage (module, hostOff) {
      if (hostOff == '已删除') return;
      this.undercarriageModule = module;
      this.underWindowShow = !this.underWindowShow;
      this.offWrong = '';
    },
    undercarriageHide () {
      this.underWindowShow = !this.underWindowShow;
      this.undercarriageing = false;
    },
    openResetIp (ip,paths,ex_files,module) {
      this.resetIpDetail = {};
      paths.map(function (item,index){
        if(!item){
          paths.splice(index,1);
        }
      })
      ex_files.map(function (item,index){
        if(!item){
          ex_files.splice(index,1);
        }
      })
      this.$set(this.resetIpDetail, 'app_id',this.app_id);
      this.$set(this.resetIpDetail, 'ips',ip);
      this.$set(this.resetIpDetail, 'modules',module);
      this.$set(this.resetIpDetail, 'paths',paths.join(','));
      this.$set(this.resetIpDetail, 'ex_files',ex_files.join(','));
      this.resetIp = !this.resetIp;
    },
    resetIpHide () {
      this.resetIp = !this.resetIp;
      this.resetIpErr = '';
    },
    resetIpDone () {
      let submitData = {
        'ips_source': 1,
        'csrfmiddlewaretoken': getCookie('csrftoken')
      };

      submitData = Object.assign({}, submitData, this.resetIpDetail);

      this.$http.post(site + 'publish_config/', submitData,{
        emulateJSON: true
      }).then(response => {
        response = response.body;
        if (response.status) {
          this.resetIp = !this.resetIp;
          this.refresh(this);
        } else {
          this.resetIpErr = response.message;
        }
      });
    },
    undercarriageDone () {
      let offModule = this.undercarriageModule;
      let self = this;

      let offData = {
        'app_id': this.app_id,
        'module': offModule,
        'csrfmiddlewaretoken': getCookie('csrftoken')
      }

      this.$http.post(site + 'host_module_off/', offData, {
        emulateJSON: true
      }).then(response => {
        // 删除中
        response = response.body;
        if (response.status) {
          self.undercarriageing = true;
          // 确认删除时，文案变成删除中，并且弹层消失
          // self.host[this.undercarriageModule]['details']['publish_status'] = '删除中';
          self.undercarriageHide();
          self.refresh(self);
        } else {
          self.offWrong = response.message;
        }
      });
    },
    refresh (obj) {
      var timestrap = +new Date();
      // 以模块为维度的请求采集项数据
      obj.$http.get(site + 'query_module_config/?timestrap='+timestrap+'&app_id='+obj.app_id+'&length='+obj.pageNum+'&offset='+obj.start+'&value='+obj.filterTxt).then(response => {
        response =response.body;
        if (response.result) {
          response.data.forEach((item, index) => {
            if(typeof item.shortview === 'undefined') item.shortview = !obj.openAll;
            if (obj.oStart == obj.start && obj.host.length > 0 && typeof obj.host[index].shortview != 'undefined' ) {
              item.shortview = obj.host[index].shortview;
            };
          });

          obj.host = response.data;
          obj.oStart = obj.start;
          obj.total = response.total_count;
          obj.queryStatus(obj);
          obj.loading = false;
        };
      })
    },
    queryStatus (obj) {
      clearInterval(window.statusTimer);
      window.statusTimer = setInterval(function () {
        let statusWatcher = {};
        let hasOff = false;
        obj.host.forEach((item) => {
          item.hosts.forEach((ipObj) => {
            switch(ipObj.publish_status)
            {
            case '删除中':
              statusWatcher[ipObj.id] = '删除中';
              hasOff = true;
              break;
            case '发布中':
              statusWatcher[ipObj.id] = '发布中';
              hasOff = true;
              break;   
            case '等待中':
              statusWatcher[ipObj.id] = '等待中';
              hasOff = true;
              break;
            }           
          })

        })
        if (hasOff) {
          let statusData = {
            'app_id':obj.app_id,
            'host_id_status':JSON.stringify(statusWatcher),
            'csrfmiddlewaretoken':getCookie('csrftoken')
          };

          obj.$http.post(site + 'query_host_status/',statusData,{
            emulateJSON: true
          }).then(response => {
            response = response.body;
            if (response.status) {
              if (response.data.result) {
                clearInterval(window.statusTimer);
                // 刷新
                var timestrap = +new Date();
                obj.$http.get(site + 'query_module_config/?timestrap='+timestrap+'&app_id='+obj.app_id+'&length='+obj.pageNum+'&offset='+obj.start+'&value='+obj.filterTxt).then(response => {
                  response = response.body;
                  if (response.result) { // 轮询结束后（删除或发布完毕）的操作
                    response.data.forEach((item,index) => {
                      item.shortview = obj.host[index].shortview;
                    })
                    obj.host = response.data;
                    obj.total = response.total_count;
                    obj.loading = false;

                    let hasOff = false;
                    obj.host.forEach((item) => {
                      item.hosts.forEach((ipObj) => {
                        switch(ipObj.publish_status)
                        {
                        case '删除中':
                          statusWatcher[ipObj.id] = '删除中';
                          hasOff = true;
                          break;
                        case '发布中':
                          statusWatcher[ipObj.id] = '发布中';
                          hasOff = true;
                          break;
                        case '等待中':
                          statusWatcher[ipObj.id] = '等待中';
                          hasOff = true;
                          break;
                        }            
                      })
                    })
                    if (hasOff) {
                      obj.refresh(obj);
                    };
                  };//end

                });
              };
            };
          })
        };
      },1000)
    },
    renderData () {
      let self = this;
      let queryParam = urlParse();
      if(queryParam.app_id){
        this.app_id = parseInt(queryParam.app_id);
      }else{
        let localId = loadFromLocal(clientName, 'app_id',false);
        if (localId) {
          this.app_id = parseInt(localId);
        } else {
          this.$router.push('/');
        }
      }
      let appDom = document.getElementById('app');
      appDom.onscroll = null;
      this.loading = true;
      self.refresh(self);
    }
  },
  created () {
    this.renderData();
  },
  components: {
    'v-search': searchbox,
    'v-win': win,
    'v-offdetail': offdetail,
  },
  beforeDestroy () {
    clearInterval(this.statusTimer);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" rel="stylesheet/scss" scoped>
.host{
  margin: auto;
  width: 100%;
  padding: 50px 20px 100px;
  max-width: 1480px;
  min-width: 1200px;
  .input-group{
    display: inline-table;
    vertical-align: middle;
  }
  .host-search{
    z-index: 2;
  }    
  .ip-tr{
    display: none;
    >td{
      border-top: none;
    }
    &.in{
      display: table-row;
    }
  }
  .table-inner tbody{
    border-bottom: none;
    background: #F5F5F5;
  }
  .no-data{
    text-align: center;
    height: 36px;
    line-height: 36px;
  }
  .fade-enter-active, .fade-leave-active {
    transition: opacity .3s
  }
  .fade-enter, .fade-leave-active {
    opacity: 0
  }
  .table-page{
    min-height: 58px;
    position: relative;
    text-align: right!important;
    ul{
      vertical-align: top;
    }
  }
  .icon-color:hover{
    color: #57a3f1;
  }
  .undercarriage-wrapper{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 50;
    .undercarriage-mark{
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.3);
    }
    .undercarriage-window{
      position: absolute;
      left: 50%;
      top: 50%;
      margin-top: -100px;
      z-index: 51;
      margin-left: -231px;
      width: 462px;
      height: auto;
      background: white;
      .title{
        height: 50px;
        line-height: 24px;
        padding: 13px 20px;
        border-bottom: 1px solid #e6e6e6;
        background-color: #f7f7f7;
        color: #666;
      }
      .close-icon{
        width: 12px;
        height: 12px;
        margin-top: 6px;
        background: url(./close.png) no-repeat center center;
        float: right;
        cursor: pointer;
      }
      .content{
        background-color: white;
        padding:20px;
        color: #666666;
      }
      .footer{
        padding: 12px;
        text-align: right;
        .king-btn{
          min-width: 70px;
        }
      }
    }
  }
  .log-pop{
      transition:all 0.7s cubic-bezier(.4,.08,.41,.98);
      background:rgba(0,0,0,0.3);
      position: fixed;
      top: 0px;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 100;
      overflow-y:hidden;
      .pop-close{
          cursor:pointer;
          font-size: 14px;
          display:block;
          position:absolute;
          width:30px;
          height:58px;
          top:0;
          right:666px;
          background: #ff6666;
          color:#fff;
          padding-left:8px;
          padding-top: 10px;
          line-height: 20px;
      }
      .new-log-host{
          z-index: 10000;
          position:absolute;
          top:0;
          right:0;
          height:100%;
          width:666px;
          background:#fff;
          overflow-y: auto;
          overflow-x: hidden; 
      }
      .new-host-title{
          font-size: 16px;
          color:#666;
          font-weight: bold;
          height: 58px;
          line-height: 58px;
          padding-left: 15px;
          border-bottom:1px solid #e6e6e6;
      }
  }
  .move-leave-active,.move-enter {
    background:rgba(0,0,0,0)!important;
    right: -666px!important;
  }
  .move-leave{
    background:rgba(0,0,0,0.3)!important;
    right: 0!important;
  }
  .mark{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.2);
  }
  .win-wrapper{
    position: fixed;
    right: 0;
    top: 54px;
    bottom: 0;
    background: black;
    width: 666px;
    .close{
      position: absolute;
      left: 0;
      top: 0;
      color: white;
    }
  }
  .toggle-all{
    border: 1px solid #ccc;
    height: 36px;
    padding-top: 10px;
    padding-left: 15px;
    padding-right: 15px;
  }
  .title{
    line-height: 36px;
  }
  .searchbox{
    height: 160px;
  }
  tr{
    cursor: default;
    td .toggle-show{
      cursor: pointer;
      display: none;
    }
    &:hover td .toggle-show{
      opacity: 1;
    }
    &.too-long{
      cursor: pointer;
      .toggle-show{
        display: inline-block;
      }
    }
  }
  tr td.content{
    max-width: 0px;
    div{
      width: 100%;
      word-break:break-all;
      white-space: nowrap;
      overflow: hidden;
      text-overflow:ellipsis;
    }
    .div-hidden{
      position: absolute;
      width: auto;
      height: 0;
      visibility: hidden;
    }
    .paths-text + i{
      margin-left: 10px ;
    }
    .fa-save{
      color: #b2beca;
    }
  }
  tr.all td.content{
    .div-show{
      white-space: inherit;
    }
  }
  .king-btn-split .king-btn.king-btn-mini{
    width: auto;
    min-width: auto;
    padding: 3px 5px;
  }
  #host_table .module-td{
    padding-bottom: 6px;
    .king-btn-mini{
      width: 70px;
      padding: 0 3px;
      margin-right: 5px;
      margin-bottom: 5px;
      line-height: 22px;
    }
    .king-btn-icon{
      width: 24px;
    }
  }
  .king-btn-split.king-disabled a,.king-btn-split.king-disabled:hover a{
      background-color: #fff;
      border-color: #e5e5e5;
      color: #e6e6e6;
      cursor: not-allowed;
  }
  .mt1{
      margin-top: 1px;
  }
  .v-hide{visibility: hidden;}
  .king-btn-split.is-disabled a, .king-btn-split[disabled] a{
      background-color: #ffffff !important;
      border-color: #e5e5e5 !important;
      color: #e6e6e6 !important;
      cursor: not-allowed;
  }
}
</style>



