<template>
  <div class="result" id="resultdom">
  	<v-search :time-round="timeRound" :my-result="result" :app-id="appId" :result-total="resultTotal" :open-all="openAll" ref="searchbox"></v-search>
    <div class="chart-wrapper" v-show="chartResult.result && chartResult.length>0">
        <div class="chart-menu">
          <span class="f20">整体趋势</span>
          <ul class="fr">
            <li class="inline-block vm" @click="changeChart('1h','sameKey')" :class="{'chart-active':timeRound == '1h'}">1小时</li>
            <li class="inline-block vm" @click="changeChart('12h','sameKey')" :class="{'chart-active':timeRound == '12h'}">12小时</li>
            <li class="inline-block vm" @click="changeChart('24h','sameKey')" :class="{'chart-active':timeRound == '24h'}">24小时</li>
          </ul>
        </div>
        <div id="chart" style="width:100%;height:240px;"></div>      
    </div>

    <div class="panel panel-default table7_demo">
        <div class="panel-body pl40">
            <span class="title f20 pull-left">日志检索结果</span>
            <form class="form-inline pull-right">
                <div class="checkbox form-hd toggle-all mr15">
                    <label @click="showOpenType">
                        <div class="fl">
                            <input type="checkbox" v-model="openAll">
                            <span class="form-checkbox-icon"></span>
                        </div>
                        <div class="fl form-control-lh">全部展开</div>
                    </label>
                </div>
                <div class="form-group mr5 checkbox form-hd">
                    <div class="mr20">
                      显示项
                      <div class="show-cols-wrapper ml5">
                          <a class="king-btn king-default" @click.stop.prevent="showOptionsWrapper">{{showOptions}}<span class="caret"></span></a>
                          <div class="show-cols" v-show="showOptionsList" @click.stop="showColsBox">
                            <div>
                              <label class="label-box">
                                  <div class="fl">
                                      <input type="checkbox" v-model="idHide">
                                      <span class="form-checkbox-icon"></span>
                                  </div>
                                  <div class="fl form-control-lh">IP地址</div>
                              </label>
                            </div>
                            <div>
                              <label class="label-box">
                                  <div class="fl">
                                      <input type="checkbox" v-model="timeHide">
                                      <span class="form-checkbox-icon"></span>
                                  </div>
                                  <div class="fl form-control-lh">时间</div>
                              </label>
                            </div>
                            <div>
                              <label class="label-box">
                                  <div class="fl">
                                      <input type="checkbox" v-model="moduleHide">
                                      <span class="form-checkbox-icon"></span>
                                  </div>
                                  <div class="fl form-control-lh">模块</div>
                              </label>
                            </div>
                             <div>
                              <label class="label-box disabled" >
                                  <div class="fl">
                                      <input type="checkbox" v-model="contentHide" disabled="disabled">
                                      <span class="form-checkbox-icon"></span>
                                  </div>
                                  <div class="fl form-control-lh" >日志内容</div>
                              </label>
                            </div>
                          </div>
                      </div>
                    </div>
                </div>
                <!-- <a class="king-btn king-default" @click="downloadLog">下载</a> -->
            </form>
        </div>
        <div>
            <!-- 带操作按钮表格-6 Start -->
            <table class="table table-hover mb0" id="result_table">
                <thead>
                    <tr class="active">
                        <th style="width:30px;"></th>
                        <th :class="{'hide-ip':!idHide}" style="width:145px">IP地址</th>
                        <th :class="{'hide-ip':!timeHide}" style="width:200px">时间</th>
                        <th :class="{'hide-ip':!moduleHide}" style="width:200px;">模块</th>
                        <th :class="{'hide-ip':!contentHide}"  disabled="disabled">日志内容</th>
                    </tr>
                </thead>
                <tbody id="datas">
                    <tr :class="{'all':!item.shortview,'too-long':item.tooLong}"  @mouseenter="checkLong($event.target,item)" style="display: table-row;"  v-for="item in result.log_list" @click="toggleShow(item)">
                        <td class="p0">
                          <span class="pl10 toggle-show">
                            <i class="log-icon-open f18 vm icon-color" v-show="item.shortview"></i><i class="log-icon-fold f18 vm icon-color" v-show="!item.shortview"></i>
                          </span>
                        </td>
                        <td :class="{'hide-ip':!idHide}">
                            <div class="log-ip" v-html="item.serverIp"></div>
                        </td>
                        <td :class="{'hide-ip':!timeHide}"><i class="log-icon-time f18 vm icon-color"></i> <span class="vm">{{item.localTime}}</span></td>
                        <td class="module-td" :class="{'hide-ip':!moduleHide}">
                          <span v-for="(m,index) in item.moduleName.split(',')"
                             class="module-name text-over-hide"
                             :title="m" v-show="moduleShow(item.shortview,index,item.moduleName)" v-if="m" v-html="m">
                          </span><span class="king-btn king-default king-btn-mini king-btn-icon" v-if="item.moduleName.split(',').length>2 && item.shortview">&middot;&middot;&middot;</span>
                        </td>
                        <td class="log-content" :class="{'hide-ip':!contentHide}">
                            <div class="div-show" v-html="item.log"></div>
                            <div class="div-hidden" v-html="item.log"></div>
                        </td>
                    </tr>
                </tbody>
            </table>
            <div v-if="loading" class="no-data"><img src="./loading_2_24x24.gif"></div>
            <div v-if="!result.total && !loading && !noLog" class="no-data"><span>{{noResult}}</span></div>
            <div class="no-data" v-if="noLog && !loading"><span>没有采集项，请到<a @click="toHost" class="link"> 采集项管理 </a>页面新建</span></div>
            <div class="text-center pt20 pb20" v-show="!loadAll && !flag">
              <img src="./loading_1_36x36.gif">
            </div>
            <!-- 带操作按钮表格-6 End -->
        </div>
    </div>
    <a class="to-top" :class="{'active':topBtn}" id="to_top" @click="toTop"><i class="fa fa-angle-up"></i></a>
  </div>
</template>

<script>
import searchbox from 'components/searchbox/searchbox';
import {urlParse} from 'common/js/util';
import {getCookie,formatDate} from 'common/js/functions';
import {saveToLocal, loadFromLocal} from 'common/js/store';
import Vue from 'vue';

let chartObj = {
              xAxis: {
                  axisLabel: {
                      inside: false,
                      textStyle: {
                          color: '#fff'
                      }
                  },
                  axisTick: {
                      show: false
                  },
                  axisLine: {
                      show: false
                  },
                  z: 10
              },
              grid: {
                  x: 80,
                  x2: 50,
                  y: 30,
                  y2: 30
              },
              tooltip: {
                  trigger: "axis"
              },
              yAxis: {
                  splitNumber: 4,
                  splitLine: {
                    lineStyle: {
                      color: '#eee'
                    }
                  },
                  axisLine: {
                      show: false
                  },
                  axisTick: {
                      show: false
                  },
                  axisLabel: {
                      textStyle: {
                          color: '#999'
                      },
                      formatter: function (value, index) {
                          // 格式化成月/日，只在第一个刻度显示年份
                          if (value < 10000) {
                            return value;
                          }
                          if (value<10000000 && value>=10000) {
                            return value/10000 + 'w';
                          }
                          if (value >= 10000000) {
                            return value/10000000 + 'kw';
                          };
                          
                      }
                  }
              },
              series: [
                  {
                      name: "数据量",
                      type: 'bar',
                      itemStyle: {
                          normal: {
                              color: new echarts.graphic.LinearGradient(
                                  0, 0, 0, 1,
                                  [
                                      {offset: 0, color: '#3EC7F5'},
                                      {offset: 0.5, color: '#3EC7F5'},
                                      {offset: 1, color: '#3EC7F5'}
                                  ]
                              )
                          },
                          emphasis: {
                              color: new echarts.graphic.LinearGradient(
                                  0, 0, 0, 1,
                                  [
                                      {offset: 0, color: '#3879D9'},
                                      {offset: 0.7, color: '#3879D9'},
                                      {offset: 1, color: '#3879D9'}
                                  ]
                              )
                          }
                      }
                  }
              ]
          };

export default {
  name: 'home',
  data () {
    return {
      result: {},
      timeHide: true,
      contentHide:true,
      moduleHide:true,
      idHide: true,
      showOptionsList: false,
      openAll: false,
      appId: null,
      flag: true,
      start: 0,
      topBtn: false,
      view: 'result',
      noResult:'',
      loading: false,
      noLog: false,
      resultTotal: 0,
      chartData: {x:[],y:[]},
      chartDataX: [],
      chartDataY: [],
      myChart: null,
      chartOption: chartObj,
      timeRound: '1h',
      chartResult: {
        result: false,
        length: 0
      }
    };
  },
  computed: {
    loadAll () {
      return this.start > this.resultTotal
    },
    showOptions () {
      let arr = [];
      if (this.idHide) {
        arr.push('IP地址');
      }
      if (this.timeHide) {
        arr.push('时间');
      }
      if (this.moduleHide) {
        arr.push('模块');
      }
      if (this.contentHide) {
        arr.push('日志内容');
      }
      if (arr.length === 0) {
        return '请选择';
      };
      return arr.join(',');
    }
  },
  methods: {
    checkLong (dom,item) {
      let div = dom.querySelectorAll('.div-show')[0];
      let divHide = dom.querySelectorAll('.div-hidden')[0];
      let divWidth = div.clientWidth;
      let divHideWidth = divHide.clientWidth;

      if (divHideWidth > divWidth || item.moduleName.split(',').length>2) {
        Vue.set(item,'tooLong',true);
      }else{
        Vue.set(item,'tooLong',false);
      }

    },
    toHost () {
      this.$router.push('/host');
    },
    fnScroll () {
      let appDom = document.getElementById('app');
      let maindom = document.getElementById('resultdom');
      let top = maindom.offsetTop;
      let mainHeight = maindom.offsetHeight;
      let winHeight = document.body.clientHeight;
      if (appDom.scrollTop > winHeight) {
        this.topBtn = true;
      } else {
        this.topBtn = false;
      }
      if (appDom.scrollTop > top + mainHeight - winHeight - 200) {
        if (this.flag) {
          this.flag = false;
          let key = loadFromLocal(clientName, 'logSearchKey', '');
 
          this.start = this.start + 30;

          this.$http.post(site + 'search_log/', {
            size:30,
            start: this.start,
            key: key,
            app_id: this.appId,
            csrfmiddlewaretoken:getCookie('csrftoken')
          },{
            emulateJSON: true
          }).then(response => {
            response = response.body;
            if (response.result) {
              this.resultTotal = response.data.total;
              response.data.log_list.forEach((item) => {
                item.shortview = !this.openAll;
              });
              this.result.log_list = this.result.log_list.concat(response.data.log_list);
              this.$nextTick(() => {
                this.flag = true;
              })
            };
          });
        };
      };
    },
    toTop () {
      let appDom = document.getElementById('app');
      appDom.scrollTop = 0;
    },
    showOptionsWrapper () {
      this.showOptionsList = !this.showOptionsList;
    },
    toggleShow (item) {
      let sText;
      if(document.selection){ // ie
        sText = document.selection.createRange().text;
      }else{
        sText = window.getSelection().toString();
      }
      if(sText)return;
      if (!item.shortview) {
        this.openAll = false;
      };
      item.shortview = !item.shortview;
    },
    showColsBox () {
    },
    showOpenType () {
      this.result.log_list.forEach((item) => {
        if (this.openAll) {
          item.shortview = false;
        } else {
          item.shortview = true;
        }
      });
    },
    moduleShow (shortview, index, module) {
      if (shortview) {
        if (module.split(',').length > 2) {
          if (index < 1) return true;
        } else {
          if (index < 2) return true;
        }
      } else {
        return true;
      }
    },
    downloadLog () {
      let key = loadFromLocal(clientName, 'logSearchKey', '');
      window.location.href = site + 'export_log?size=10000&start='+this.start+'&key='+key+'&app_id='+this.appId;
      // window.open(site + 'export_log?size=10000&start='+this.start+'&key='+key+'&app_id='+this.appId);

    },
    renderData () {
      let queryParam = urlParse();
      let self = this;
      this.noResult = '';
      this.openAll = false;

      if(queryParam.app_id){
        this.appId = parseInt(queryParam.app_id);
      }else{
        let localId = loadFromLocal(clientName, 'app_id',false);
        if (localId) {
          this.appId = parseInt(localId);
        } else {
          this.$router.push('/');
        }
      }

      if (typeof queryParam.key === 'undefined') {queryParam.key=''};
      let key = loadFromLocal(clientName, 'logSearchKey', '');

      let logData = {
        'size':30,
        'start': this.start,
        'key': key,
        'app_id': this.appId,
        'csrfmiddlewaretoken':getCookie('csrftoken')      
      }

      this.loading = true;
      this.$http.post(site + 'search_log/', logData,{
           emulateJSON: true
        }).then(response => {
        response = response.body;
        this.loading = false;
        if (response.result) {
          response.data.log_list.forEach((item) => {
            item.shortview = true;
          });
          this.result = response.data;
          if (response.code === '20012') {
            this.noResult = response.message;
          }else{
            this.noResult = '暂无数据！';
          }
          this.noLog = false;
          document.addEventListener('click',function () {
            self.showOptionsList = false;
          })
          let appDom = document.getElementById('app');
          appDom.onscroll = function () {
            if (self.view === 'result') {
              self.fnScroll();
            };
          }
        }else{
          this.result = {};
          this.noLog = true;
        }
      });
      // 图表请求数据传参
      let chartParm = {
         'app_id': ''+this.appId,
         'keyword': key,
         'date_inteval': this.timeRound,
         'csrfmiddlewaretoken': getCookie('csrftoken')
      }
      this.$http.post(site + 'get_chart_data/',chartParm,{
         emulateJSON: true
      }).then(response => {
        response = response.body;
        if (response.result && response.data.chart_data.length>0) {
          this.chartResult.result = true;
          this.chartResult.length = response.data.chart_data.length;
          this.$nextTick(() => {
            // 数据格式转换
            if (response.data.chart_data.length > 100) {
              response.data.chart_data = response.data.chart_data.slice(response.data.chart_data.length - 100);
            };
            response.data.chart_data.map(function (item,index) {
              let date = new Date(item[0]);
              self.chartDataX[index] = formatDate(date,'yyyy-MM-dd hh:mm');
              self.chartDataY[index] = item[1];
            });

            let dataAxis = this.chartDataX;
            let data = this.chartDataY;
            // 绘制图表
            this.chartOption.xAxis.data = dataAxis;
            this.chartOption.series[0].data = data;
            this.renderChart(this,dataAxis,data)
          })
        } else {
          this.chartResult.result = false;
          this.chartResult.length = 0;
        }
      })
    },
    changeChart (str,type) {
      if (type === 'sameKey' && this.timeRound === str) {return};
      let self = this;
      let key = loadFromLocal(clientName, 'logSearchKey', '');
      self.timeRound = str;
      // 图表请求数据传参
      let chartParm = {
         'app_id': ''+this.appId,
         'keyword': key,
         'date_inteval': str,
         'csrfmiddlewaretoken': getCookie('csrftoken')
      }
      // 切换维度
      this.$http.post(site + 'get_chart_data/',chartParm,{
         emulateJSON: true
      }).then(response => {
        response = response.body;
        if (response.result && response.data.chart_data.length>0) {
          this.chartResult.result = true;
          this.chartResult.length = response.data.chart_data.length;
          this.$nextTick(() => {
            // 数据格式转换
            self.chartDataX = [];
            self.chartDataY = [];
          // 数据格式转换
            if (response.data.chart_data.length > 100) {
              response.data.chart_data = response.data.chart_data.slice(response.data.chart_data.length - 100);
            };
            response.data.chart_data.map(function (item,index) {
              let date = new Date(item[0]);
              self.chartDataX[index] = formatDate(date,'yyyy-MM-dd hh:mm');
              self.chartDataY[index] = item[1];
            });
            // 绘制图表
            this.chartOption.xAxis.data = this.chartDataX;
            this.chartOption.series[0].data = this.chartDataY;
            if (this.myChart) {
              this.myChart.clear();              
            };
            this.renderChart(this,this.chartDataX,this.chartDataY)
          })
        } else {
          this.chartResult.result = false;
          this.chartResult.length = 0;
        }
      })
    },
    renderChart (self,dataAxis,data) {
      // 基于准备好的dom，初始化echarts实例
      self.myChart = echarts.init(document.getElementById('chart'));
      // 绘制图表
      self.myChart.setOption(self.chartOption);
      // Enable data zoom when user click bar.
      // var zoomSize = 6;
      // self.myChart.on('click', function (params) {
      //     self.myChart.dispatchAction({
      //         type: 'dataZoom',
      //         startValue: dataAxis[Math.max(params.dataIndex - zoomSize / 2, 0)],
      //         endValue: dataAxis[Math.min(params.dataIndex + zoomSize / 2, data.length - 1)]
      //     });
      // });
      window.onresize = self.myChart.resize;
      // end
    }
  },
  created () {
    this.renderData();
  },
  components: {
    'v-search': searchbox
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" rel="stylesheet/scss" scoped>
.chart-wrapper{
  background: white;
  padding-top: 20px;
  margin-bottom: 20px;  
  border:1px solid #ddd;
  .chart-menu{
    padding-left: 40px;
    padding-right: 40px;
    ul{
      font-size: 0px;
    }
    li{
      font-size: 12px;
      border: 1px solid #ddd;
      padding:3px 12px;
      margin-left: -1px;
      color: #999;
      cursor: pointer;
      &:hover{
        background: #F5F5F5;
      }
    }
    .chart-active{
      background: #F5F5F5;
      cursor: default;
    }
  }
}
.result{
  position: relative;
  margin: auto;
  width: 100%;
  padding: 0 20px;
  max-width: 1480px;
  min-width: 1200px;
  .searchbox{
    height: 160px;
  }
  .title{
    line-height: 36px;
  }
  .toggle-all{
    border: 1px solid #ccc;
    height: 36px;
    padding-top: 10px;
    padding-left: 15px;
    padding-right: 15px;
  }
  .link{
    cursor: pointer;
  }
  .no-data{
    text-align: center;
    height: 53px;
    line-height: 36px;
    padding: 8px;
  }
  .to-top{
    position: fixed;
    right: 30px;
    bottom: 10px;
    width: 48px;
    height: 48px;
    line-height: 42px;
    font-size: 36px;
    text-align: center;
    background: #CCCCCC;
    color: white;
    cursor: pointer;
    opacity: 0.7;
    display: none;
    &.active{
      display: block;
    }
    &:hover{
      opacity: 1;
    }
  }
  .form-hd{
    display: inline-block!important;
    margin-top: 0!important;
    margin-bottom: 0!important;
    vertical-align: top;
  }
  .show-cols-wrapper{
    position: relative;
    display: inline-block;
    &>a{
      height: 36px;
      width: 200px;
      background: white;
      cursor: default;
      text-align: left;
      .caret{
        position: absolute;
        right:10px;
        top: 16px;
      }
    }
    .show-cols{
      position: absolute;
      top: 34px;
      left: 0;
      width: 100%;
      background: white;
      border: 1px solid #ccc;
      &>div{
        label{
          display: block;
          width: 100%;
          height: 100%;
          overflow: hidden;
          padding:10px 8px 5px;
          vertical-align: top;
        }
        .label-box.disabled{
          cursor: not-allowed;
        }
      }
      &>div+div{
        border-top: 1px solid #ccc;
      }
    }
  }

  #result_table{
    .module-td{
      padding-bottom: 6px;
    }
    tr{
      .toggle-show{
        opacity: 0;
      }
      .hide-ip{
        display: none;
      }
    }
    tr.too-long{
      cursor: pointer;
      .toggle-show{
        display: inline-block;
      }
      .log-content{
        cursor: pointer;
      }
    }
    .too-long:hover .toggle-show{
        opacity: 1;
    }
    tr td.log-content{
      max-width: 0px;
      div{
        width: 100%;
        white-space: nowrap;
        overflow: hidden;
        text-overflow:ellipsis;
      }
      .div-hidden{
        position: absolute;
        width: auto;
        height: 1px;
        overflow: hidden;
        visibility: hidden;
      }
    }
    tr.all td.log-content{
      div{
        white-space: inherit;
        text-overflow: inherit;
        word-break: break-word;
      }
    }
  }

}
</style>



