export function saveToLocal (id, key, val) {
  let logsearch = window.localStorage.__logsearch__;
  if (!logsearch) {
    logsearch = {};
    logsearch[id] = {};
    logsearch[id][key] = val;
  } else {
    logsearch = JSON.parse(logsearch);
    if (!logsearch[id]) {
      logsearch[id] = {};
    }
  }
  logsearch[id][key] = val;
  window.localStorage.__logsearch__ = JSON.stringify(logsearch);
};

export function loadFromLocal (id, key, def) {
  let logsearch = window.localStorage.__logsearch__;
  if (!logsearch) {
    return def;
  }
  logsearch = JSON.parse(logsearch)[id];
  if (!logsearch) {
    return def;
  }
  let ret = logsearch[key];
  return ret || def;
}



