<template>
  <div id="app">
    <v-header :app-list="appList" :app-id="appId" ref="head"></v-header>
    <div class="main">
      <router-view :prop="appObj" ref="innerview" :app-list="appList"></router-view>
    </div>
    <v-footer></v-footer>
  </div>
</template>

<script>
import header from 'components/header/header.vue';
import footer from 'components/footer/footer.vue';
import {getCookie} from 'common/js/functions';
import {saveToLocal, loadFromLocal} from 'common/js/store';

export default {
  name: 'app',
  data () {
    return {
      appObj:{},
      appList:[],
      appId:null
    }
  },
  methods: {
  },
  components: {
    'v-header': header,
    'v-footer': footer,
  },
  created () {
    let localId =  loadFromLocal(clientName, 'app_id',false);
    let self = this;
    document.onclick = function (event) {
        self.$refs.head.isOpen = false;
    };
    if(localId){
        this.appId = parseInt(localId);
    }
    this.$http.post(site + 'app_list/',{csrfmiddlewaretoken:getCookie('csrftoken')},{
      emulateJSON: true
    }).then(response => {
        response = response.body;
        if (response.result) {
            this.appList = response.data;
            if (!localId) {
                this.appId = parseInt(this.appList[0]['app_id']);
                this.$refs.head.myId = this.appId;
                saveToLocal(clientName, 'app_id', parseInt(this.appId));
            };
        };
    })
  }
};
</script>

<style>
#app{
  height: 100%;
}
.main{
  min-height: calc(100% - 170px);
  padding-bottom: 1px;
  overflow: hidden;
  width: 100%;
  min-width: 1200px;
}
</style>



