<template>
  <div class="home">
        <div class="search-box tc">
        	<div class="bg" v-bind:style="{backgroundImage:imgsUrl.bgImg}"></div>
        	<div class="content">
	            <img  src="./search-icon.png" height="64" width="117">
	            <div class="search-button-arrow">
	                <input class="search-input" type="text" v-model="inputVal" @keypress.enter="search" placeholder="搜索">
	                <button @click="search">搜索一下</button>
	            </div>
	            <div class="history-wrapper" v-if="historyList.length != 0">
              <table class="table table-bordered history-list mb50"> 
                  <thead> 
                      <th>关键词</th>
                      <th>结果条数</th>
                      <th>耗时（秒）</th>
                      <th class="tc">操作</th>   
                  </thead>
                  <tbody> 
                      <tr v-for="(h,index) in historyList" v-if="index<20">
                        <td class="key-word text-over-hide"><span :title="h.search_key">{{h.search_key}}</span></td>
                        <td>{{h.total}}</td>
                        <td> {{h.took?h.took:0}} </td>
                        <td class="tc"><a @click="searchHistory(h.search_key)">再次查询</a></td>
                      </tr>
                  </tbody>
              </table>
	            </div>
        	</div>
        </div>
        <transition name="fade">
          <div class="mark" v-show="noHost"></div>
        </transition>
        <transition name="fade">
          <div class="no-host" v-show="noHost">
              <header>温馨提示<button type="button" @click="closeNoHost" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">×</span></button></header>
              <section>
                <p>检测到您的业务采集项没有任何内容，请点击下面按钮跳转到采集项管理，新建采集项！</p>
              </section>
              <footer>
                <a class="king-btn king-primary king-btn-large" @click="goToHost">新建采集项</a>
              </footer>
          </div>
        </transition>
  </div>
</template>

<script>
import {getCookie} from 'common/js/functions.js';
import {saveToLocal, loadFromLocal} from 'common/js/store';

export default {
	name: 'home',
	data () {
    return {
      appId: null,
      historyList: [],
      inputVal: '',
      view:'home',
      imgsUrl:{
        homepic: site + 'static/images/homepic.jpg',
        bgImg: 'url('+site + 'static/images/bannerbg.png'+')'
      },
      noHost:false
    };
	},
  methods: {
  	search () {
      saveToLocal(clientName, 'logSearchKey', this.inputVal);
			this.$router.push('/result');
  	},
		searchHistory (val) {
      saveToLocal(clientName, 'logSearchKey', val);
      this.$router.push('/result');
		},
    goToHost () {
      this.$router.push('/host');
      saveToLocal(clientName, 'logSearchNoticed', true);
    },
    closeNoHost () {
      this.noHost = false;
      saveToLocal(clientName, 'logSearchNoticed', true);
    },
    renderData () {
      let localId =  loadFromLocal(clientName, 'app_id',false);
      if(localId){
        this.appId = parseInt(localId);
      }
      
      let appDom = document.getElementById('app');
      appDom.onscroll = null;

      this.$http.post(site + 'get_all_history_keys/',{csrfmiddlewaretoken:getCookie('csrftoken'), app_id:this.appId},{
        emulateJSON: true
      }).then(response => {
          response = response.body;
          if (response.result) {
            this.historyList = response.data;
          };
      });

      this.$http.post(site + 'check_app_config/',{csrfmiddlewaretoken:getCookie('csrftoken'), app_id:this.appId},{
              emulateJSON: true
            }).then(response => {
          response = response.body;
          if (response.result) {
          }else{
            let noticed = loadFromLocal(clientName, 'logSearchNoticed',false);
            if(!noticed){
              this.noHost = true;
            }
          }
      })
    }
  },
	created(){
    this.renderData();
	},
  mounted () {
    let mainDom = document.querySelectorAll('.main')[0];
    mainDom.style.background = '#313b4c';
  },
  beforeDestroy () {
    let mainDom = document.querySelectorAll('.main')[0];
    mainDom.style.background = '#f7f9fc';
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" rel="stylesheet/scss" scoped>
.home{
	min-width:1200px;
  .fade-enter-active, .fade-leave-active {
    transition: opacity .3s
  }
  .fade-enter, .fade-leave-active {
    opacity: 0
  }
  .mark{
    background: rgba(0, 0, 0, 0.3);
    position: fixed;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 100;
  }
  .no-host{
    position: absolute;
    z-index: 101;
    width: 620px;
    height: auto;
    background: white;
    box-shadow: 0 0 2px #ccc;
    border-radius: 4px;
    border-top: 3px solid #57a3f1;
    padding: 0 15px;
    top: 50%;
    left: 50%;
    margin-left: -310px;
    margin-top: -125px;
    button{
      margin-top: 16px;
    }
    section{
      padding: 24px 10px;
      p{
        line-height: 30px;
        font-size: 16px;
        margin-bottom: 0;
      }
    }
    footer{
      text-align: center;
      padding-top: 10px;
      padding-bottom: 30px;
    }
    header{
      height: 60px;
      line-height: 60px;
      font-size: 20px;
      text-align: center;
      border-bottom: 1px dashed #ccc;
    }
  }
	.footer-bg{
		width:100%;
		min-width:1200px;
		img{
			display: block;
			margin: auto;
		}
	}
	.search-box{
		position: relative;
		padding-top: 139px;
		background: #313b4c;
		.content{
			position: relative;
			z-index: 11;
		}
		.bg{
			position: absolute;
			left:0;
			top: 0;
			width: 100%;
			height: 100%;
			background-repeat:no-repeat;
      background-position:center 0;
      z-index: 11;
		}
	}
	.search-box input{
	    font-size:18px;
	    padding:0 20px;
	    width: 570px;
	    height:48px;
	    border-radius:2px 0 0 2px;
	    border:none;
	    line-height:48px;
	    vertical-align: top;
	}
	.search-box button{
	    background:#4b9fff;
	    width:120px;
	    height:48px;
	    border:none;
	    color:#fff;
	    font-size:18px;
	    border-radius:0 2px 2px 0;
	    line-height:48px;
	    vertical-align: top;
	}
	.search-box .search-button-arrow{
	    font-size:0;
	    margin:50px 0 20px 0;
	}
	.history-wrapper{
		width: 690px;
		margin: auto;
    text-align:left;
    font-size:14px;
    color:#738094;
    overflow: hidden;
		.history-list{
        color:#8292a7;
        border: 1px solid #464f5e;
        th{
          background: #3d4757;
          height: 42px;
          text-align: left;
          padding:0 20px; 
        }
        td{
          padding-left:20px;
          padding-right: 20px;
          text-align: left;
          border-color: #464f5e;
        }
        .key-word{
            width: 250px;
            max-width: 250px;
        }
        a{
          color: #4b9fff;
          &:hover{
            opacity: 1;
            text-decoration: underline;
            cursor: pointer;
          }
        }
		}
	}
}
</style>



